body {
  background-color: rgb(59, 55, 85);
  color: white;
  font-family: monospace;
  margin: auto;
  max-width: 700px;
  padding: 0.5rem;
}
.para {
  font-size: 15px;
}
button {
  cursor: pointer;
  background: #cbd5e1;
  border: 1px solid black;
  margin: 1rem 0.3rem;
  padding: 0.8rem;
  border-radius: 1rem;
}
ul {
  padding-inline-start: 0%;
}

li {
  list-style: none;
  border: 1px solid black;
  max-width: 60%;
  padding: 1rem;
  margin-top: 1rem;
  background: #475569;
  border-radius: 1rem;
}
